import * as React from 'react';
import { ZhiFactory } from '@ali/4ever-cangjie';
import moFactory from '../mo/index';
import Paragraph from '../components/ReadParagraph';
import { Component } from '../components/styled';
import { queries } from './queries';

export const factory: ZhiFactory = () => {
  return {
    ...moFactory({}),
    queries,
    renderNode: {
      paragraph: (props) => {
        const { controller, attributes, children, node } = props;

        const content = controller.query('renderParagraph', props as any);
        if (content) {
          return content;
        }

        return (
          <Paragraph
            {...attributes}
            node={node as any}
            Component={Component}
          >
            {children()}
          </Paragraph>
        );
      },
    },
  }
}

export default factory;
